import { CST } from "../CST";
import Character from "../resources/characters/Character";
import _ from "underscore";

export class PlayScene extends Phaser.Scene {
  constructor() {
    super({
      key: CST.SCENES.PLAY
    });
  }
  //optional methods
  init() {
    console.log("play scene!!!");
  }
  preload() {
    this.anims.create({
      key: "dazzle",
      frameRate: 10,
      frames: this.anims.generateFrameNames("daze", {
        prefix: "daze0",
        suffix: ".png",
        start: 0,
        end: 41
      })
    });

    const hooded = new Character(
      this,
      "hooded",
      64,
      64,
      "characters",
      "hooded",
      10,
      {
        top: _.range(104, 112 + 1),
        right: _.range(143, 151 + 1),
        bottom: _.range(130, 138 + 1),
        left: _.range(117, 125 + 1)
      }
    );
    hooded.preload();

    const mandy = new Character(
      this,
      "mandy",
      64,
      64,
      "characters",
      "mandy",
      10,
      {
        top: _.range(325, 332 + 1),
        right: _.range(416, 424 + 1),
        bottom: _.range(403, 411 + 1),
        left: _.range(390, 398 + 1)
      }
    );
    mandy.preload();
  }

  //required!
  create() {
    //this.scene.start(CST.SCENES.MENU, "hello from loadscene");
    //this.scene.launch();
    let pimple: Phaser.GameObjects.Sprite = this.add.sprite(
      100,
      100,
      "daze",
      "daze015.png"
    );
    pimple.play("dazzle");

    let hooded: Phaser.GameObjects.Sprite = this.add.sprite(200, 200, "hooded");
    //you can pass his obj to window, so you can debug it easier

    //mandy is our npc
    let mandy: Phaser.GameObjects.Sprite = this.add.sprite(300, 500, "mandy");

    //lets make her walk randomly

    setInterval(() => {
      let directions = ["top", "right", "bottom", "left"];

      let n = Math.floor(Math.random() * directions.length);
      console.log(`mandy n is ${n}`);
      mandy.play(directions[n]);
    }, 3000);

    //@ts-ignore
    window.hooded = hooded;
  }
}
